@font-face {
  src: url("DMSans-Bold.306cbdd3.ttf"), url("DMSans-Bold.306cbdd3.ttf");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Medium.b8d1a1bf.ttf"), url("DMSans-Medium.b8d1a1bf.ttf");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Regular.331f979e.ttf"), url("DMSans-Regular.331f979e.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Italic.ed07c4fe.ttf"), url("DMSans-Italic.ed07c4fe.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: italic;
}

html {
  min-width: 320px;
}

body {
  letter-spacing: .02em;
  background-color: var(--light-theme-background);
  width: 100vw;
  font-family: DM Sans, sans-serif;
  font-size: 12px;
}

.list {
  list-style: none;
}

.link {
  font-family: inherit;
  text-decoration: none;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
  font-family: DM Sans, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.container {
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.sidebar {
  flex-direction: column;
  align-items: center;
  row-gap: 40px;
  display: flex;
}

@media screen and (max-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 320.4px) and (max-width: 767.8px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439.8px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .sidebar {
    flex-direction: row;
    justify-content: space-between;
    margin-top: 40px;
    margin-bottom: 60px;
    margin-left: 0;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    flex-direction: row;
    padding-left: 24px;
    padding-right: 24px;
  }

  .sidebar {
    flex-direction: column;
    row-gap: 142px;
  }
}

:root {
  --logo-theme-light: #000;
  --logo-theme-dark: #f3f3f3;
  --dark-theme-background: #202024;
  --light-theme-background: #f6f6f6;
  --text-dark-color: #111;
  --text-light-color: #b4afaf;
  --text-white-color: #fff;
  --accent-color1: #4f2ee8;
  --accent-color2: #eac645;
  --accent-color3: #fff;
  --main-font: "DM Sans";
  --header-background: #fff;
  --footer-background: #fff;
  --transition: 500ms cubic-bezier(.4, 0, .2, 1);
}

.header {
  width: 100vw;
  z-index: 100;
  background-color: var(--accent-color3);
  transition: background-color var(--transition);
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  padding-top: 17px;
  padding-bottom: 16px;
  position: sticky;
  top: 0;
  right: 0;
}

.header .container {
  flex-direction: row;
}

.header-container {
  align-items: center;
  display: flex;
}

.logo-link {
  letter-spacing: -.04em;
  color: var(--logo-theme-light);
  align-items: center;
  gap: 8px;
  font-weight: 500;
  display: flex;
}

.burger-menu {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  line-height: 0;
}

.burger-logo {
  stroke: #000;
}

.navigation {
  display: none;
}

.mobile-navigation {
  margin-top: 126px;
  margin-left: 108px;
}

.mobile-home-link {
  letter-spacing: -.04em;
  color: var(--accent-color3);
  transition: background-color var(--transition), font-weight var(--transition);
  border-radius: 36px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  display: inline-flex;
}

.mobile-shopping-link {
  letter-spacing: -.04em;
  color: var(--accent-color3);
  transition: background-color var(--transition), font-weight var(--transition);
  border-radius: 36px;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  display: inline-flex;
}

.mobile-home-link:hover, .mobile-home-link:focus {
  background-color: var(--accent-color2);
  transition: font-weight var(--transition);
  font-weight: 700;
}

.mobile-shopping-link:hover, .mobile-shopping-link:focus {
  background: var(--accent-color2);
  font-weight: 700;
}

.mobile-shopping-bag-icon {
  fill: var(--accent-color3);
  transition: var(--transition);
}

.mobile-home-link.mobile-selected, .mobile-shopping-link.mobile-selected, .mobile-shopping-bag-icon.mobile-selected {
  background-color: var(--accent-color2);
  color: var(--text-dark-color);
  fill: var(--text-dark-color);
  font-weight: 700;
}

.user-login {
  display: none;
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 11px;
    padding-bottom: 10px;
  }
}

@media screen and (min-width: 768px) {
  .burger-menu {
    display: none;
  }

  .navigation {
